/*! tailwindcss v4.1.12 | MIT License | https://tailwindcss.com */
.container {
  width: 100%;
}
.mx-auto {
  margin-inline: auto;
}
.flex {
  display: flex;
}
.grid {
  display: grid;
}
.min-h-screen {
  min-height: 100vh;
}
.items-center {
  align-items: center;
}
.justify-center {
  justify-content: center;
}
.rounded-full {
  border-radius: calc(infinity * 1px);
}
.text-center {
  text-align: center;
}
@layer base {
  :root {
    --cream: #F5F1E8;
    --gold: #D4AF37;
    --dark-gray: #2A2A2A;
  }
  * {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
  }
  body {
    color: var(--dark-gray);
    background: var(--cream);
  }
  html {
    scroll-behavior: smooth;
  }
}